import { API_URL } from './url';
import axios from 'axios';
import { authHeader } from './config';

export const users = () => {
  return axios.get(`${API_URL}users`, {
    headers: authHeader(),
  });
};

export const user = (registrationId) => {
  return axios.get(`${API_URL}users/${registrationId}`, {
    headers: authHeader(),
  });
};

export const manage = (params) => {
  return axios.post(`${API_URL}users/restriction`, params, {
    headers: authHeader(),
  });
};

export const userP2P = (params) => {
  return axios.get(`${API_URL}users/p2p`, params, {
    headers: authHeader(),
  });
};

export const userTradeCrypto = (params) => {
  return axios.get(`${API_URL}users/trade`, params, {
    headers: authHeader(),
  });
};

export const userTransfer = (params) => {
  return axios.get(`${API_URL}users/transfer`, params, {
    headers: authHeader(),
  });
};

export const userReceived = (params) => {
  return axios.get(`${API_URL}users/received`, params, {
    headers: authHeader(),
  });
};

export const tradeCrypto = () => {
  return axios.get(`${API_URL}btc/txs/bought`, {
    headers: authHeader(),
  });
};

export const p2p = () => {
  return axios.get(`${API_URL}btc/txs/p2p`, {
    headers: authHeader(),
  });
};

export const received = () => {
  return axios.get(`${API_URL}btc/txs/received`, {
    headers: authHeader(),
  });
};

export const transfer = () => {
  return axios.get(`${API_URL}btc/txs/transfer`, {
    headers: authHeader(),
  });
};
